.block {
    display: block;
    position: relative;
    width: 97%;
    padding-bottom: 107%;
    height: 0;
    &__image {
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 90%;
    height: 90%;
    object-fit: contain;
    }
} 